.skills-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: auto;
  margin-left: auto;
  width: 100%;
  height: 100%;
  border-bottom: 1px solid #aaa;
  padding-bottom: 5px;
}

.skills-heading {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-content: center;
}

.skills-list {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
}

.heading {
  font-weight: bolder;
}

.name-enter {
  opacity: 0.01;
}

.name-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-in;
}

.name-exit {
  opacity: 1;
}

.name-exit-active {
  opacity: 0.01;
  transition: opacity 300ms ease-in;
}

form {
  display: flex;
  align-items: center;
}

.add-button {
  background: url('../Assets/add.svg');
  border: 0;
  display: block;
  height: 20px;
  width: 20px;
  margin-left: 3px;
  cursor: pointer;
}
